
$mainOrange:  #ef581f;

body {
  background-color: whitesmoke;
}

a:hover {
  text-decoration: none;
}

p {
  font-family:"Roboto",sans-serif !important;
}

h1,h2,h3,h4,h5,h6{
  font-family:"Roboto",sans-serif !important;
  color: $mainOrange;
}

h4{
  font-size: 14px;
}

.thumbnail{
  display:flex;
  justify-content:center;
  align-items:center;
  overflow:hidden;
  max-height: 145px;
}

.thumbnail .img-responsive img{
  flex-shrink:0;
  min-width:100%;
  min-height:100%;
  margin: 0 0;
}

.news-delete-button{
  float: right;
  height: 20px;
  font-size: 10px;
  background-color: $mainOrange;
  border-color: $mainOrange;
}

.news-delete{
  margin: 0;
  float: right;
}

#map-container {
  height: 250px
}
/* Navigation Menu */
nav {
  font-family:"Roboto",sans-serif !important;
  font-size: 21px;
  margin-bottom: 10px;
  border-bottom: 1px solid $mainOrange;
}
div.nav-menu{
  padding: 0 0;
  height: 130px;
  margin-top: 10px;
  display: -webkit-flex;
  position: relative;
}

div.nav-menu ul{
  margin:0;
  padding: 0;
  position: absolute;
  bottom: 0;
}

div.nav-menu ul li{
  list-style: none;
  float: left;
}

div.nav-menu ul li:first-child{
  margin-left: 130px;
}

div.nav-menu ul li a:link,
div.nav-menu ul li a:visited{
  text-align: center;
  display: block;
  padding: 10px 15px;
  text-decoration: none;
  color: black;
}

div.nav-menu ul li:last-child{
  margin-right: 0;
  padding-right: 0;
}

div.nav-menu ul li a:hover{
  color: white;
  background-color: $mainOrange;
}

div.nav-menu ul li.active-link a{
  color: white;
  background-color:  $mainOrange;
}

div.nav-menu #header-logo{
  position: absolute;
  top: 15px;
}
.menu-trigger{
  margin: 0 -15px;
  display: none;
}

@media screen and (max-width: 740px){
  .menu-trigger{
    display: block;
    color: white;
    background-color:$mainOrange;
    text-align: right;
    cursor: pointer;
    padding: 10px;
  }

  nav{
    border-bottom: none;
  }

  div.nav-menu {
    height: auto;
    display: none;
    position: relative;
  }

  div.nav-expanded {
    display: block;
  }

  div.nav-menu ul{
    text-align: left;
    position: relative;
  }

  div.nav-menu ul li{
    float: none;
    border-bottom: 1px solid $mainOrange;
  }

  div.nav-menu ul li:last-child{
    border-bottom: none;
  }

  div.nav-menu ul li:first-child{
    margin-left: 0;
  }
  div.nav-menu a#header-logo{
    visibility: hidden;
    display: none;
  }

  div.nav-menu ul li a:link,
  div.nav-menu ul li a:visited{
    padding: 3px;
    margin-top: 0;
    margin-right: 0;
  }
}

/* End of Navigation Menu */

/* Start of news panel */

.newsPanel {
  padding: 5px;
  margin-bottom: 10px;
  margin: 15px 0;
  border-left: 1px solid $mainOrange;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, .10);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .10);
}
.newsPanel-heading {
  padding-top: 5px;
  font-family:"Roboto",sans-serif !important;
  border-bottom: 1px solid $mainOrange;
  padding-bottom: 5px;
  font-weight: 600;
}

.newsPanel-body{
  padding-top: 5px;
}

.newsPanel-footer{
  padding-top: 5px;
  font-weight: 200;
  text-align: right;
  font-style: italic;
}

/* End of news panel */

/* Start of carousel */
.carousel-inner > .item > img,
.carousel-inner > .item > a > img {
  width: 100%;
  margin: auto;
}
.carousel .item {
  height: auto;
  max-height: 400px;
}

/* End of carousel */


/* Start of rooms */
.rooms .row {
  padding: 20px 0;
  margin-bottom: 20px;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, .10);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .10);
}
.rooms .row:last-child{
  margin-bottom: 0;
}
.rooms .row img {
  width: 100%;
  border-radius: 10px;
  display: block;
  margin: 0 auto;

}
.rooms .row button {
  padding: 14px 24px;
  border: 0 none;
  font-weight: 700;
  letter-spacing: 1px;
  border-radius: 14px;
  text-transform: uppercase;
}

/* End of rooms */